// Member.js
import Data from "./Data";

export function image(img, id, alt) {
  return (
    <div className={"back-image-container"}>
      <img src={img} 
          id={"backImg-"+id} 
          style={{maxWidth:"50vmin"}} 
          className={"App-image"} 
          alt={alt} />
    </div>
 );
}

function Member(idx) {
  let data = Data[idx];
    return(
    <section id={idx + "-section"}>
      <header id={idx + "-header"}>
        <h2>{data.name}</h2>
      </header>
      <main id={idx + "-main"}>
        {image(data.img, idx+"-img", data.alt)}
        <h3>{idx.toUpperCase()[0]}</h3>
      <p id={idx + "-bio"} className="bio">
        {data.bio}
      </p>
    </main>
  </section>
);
}

export default Member;