// Events.js
import eventsData, {Employer, Place, Institution, Broadcast} from "./eventsData";
const evt = eventsData.reverse();
const Events = evt.map((e) => {
    // console.log("Event", e);
    let title = e.title;
    let text = "...";
    if (e.kind===2) {
        title += " (Sofware design)";
    } else {
        if (e.employer!==-1) text += "Together with " + Employer[e.employer];
        if (e.Broadcast!==-1) { 
            text = "Live stream broadcasted from " + Broadcast[e.broadcast] + " ";
        } else {
            text = "Filmed performance ";
        }
        if (e.institution!==-1) text += " at " + Institution[e.institution];
        if (e.location!==-1) text += ". " + Place[e.location];
    }
    let id = "id"+e.kind.toString()+e.employer+e.location+e.year+e.month+e.day;
    // the actual event div structure
    return(
        <div 
            id={id} 
            key={id} 
            className="Event-item"
        >
            <div className="Event-date">
                <code>{e.year+" | "+e.month+" | "+e.day}</code>
            </div>
            <div className="Event-title">
                {title}
            </div>
            <div className="Event-text">
                {text}
            </div>
        </div>
    );
});
// console.log(Events);
export default Events;
